@import "variables";

@font-face {
  font-family: 'smurf';
  src:
    url('#{$icomoon-font-path}/smurf.ttf?mw0uu3') format('truetype'),
    url('#{$icomoon-font-path}/smurf.woff?mw0uu3') format('woff'),
    url('#{$icomoon-font-path}/smurf.svg?mw0uu3#smurf') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="sm-"], [class*=" sm-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'smurf' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

